import React from "react";

import './introHome.css';
import imgIntroHome from '../../../../assets/homepage/intro/introHome.png';

const introHome = () => {
    return(
        <div className="introHome">
            <div className="text-intro">
                <p className="title-intro">
                    Este  um espao dedicado ao cuidado de pastores e missionrios(as).
                </p>
                <p className="description-intro">
                Sabemos que o ministrio exige uma dedicao constante e intensa, muitas vezes demandando grande esforo fsico, mental e espiritual. 
                Porm, cuidar de si mesmo  fundamental para continuar servindo com excelncia. 
                 imprescindvel preservar sua sade em todas as dimenses  fsica, emocional e espiritual  para que sua caminhada seja plena e sustentvel.
                </p>
                <p className="description-intro">
                Estamos aqui para oferecer apoio, orientao e recursos que fortalecero tanto o seu corpo quanto a sua mente. 
                Assim, voc poder continuar cumprindo sua misso com alegria, sade e um vigor renovado que refletir o amor e o cuidado de Deus em sua vida.
                </p>
            </div>
            <div className="intro-img">
                <img className="img-text" src={imgIntroHome} alt="Imagem de Introduo - Corao Saudvel"/>
            </div>
        </div>
    );  
};

export default introHome;