import React, {useState} from 'react';
import './footer.css';
import imgFooterLogo from '../../assets/logo/logo-secundary.png';
import imgCollaboratorsDOMLogo from '../../assets/collaborators/dom-logo.png';
import iconEmail from "../../assets/icons/apps/gmail.svg";
import iconInstagram from "../../assets/icons/apps/instagram.svg";
import iconFacebook from "../../assets/icons/apps/facebook.svg";
import Divider from "../divider/Divider.jsx";
import LanguageSelector from '../languageSelector/LanguageSelector.jsx';



const Footer = () => {
    const [language, setLanguage] = useState('pt-BR');
    return (
        <div className="footer">
            <div className="footer-align-right">
                <div className="footer-introducion">
                    <div className="select-language">
                        <LanguageSelector language={language} setLanguage={setLanguage} />
                    </div>
                    <div className="footer-panel-icon">
                        <a href="/" target="_blank" rel="noopener noreferrer">
                            <img className="footer-icon" src={iconInstagram} alt="cone do Instagram" />
                        </a>
                        <a href="/" target="_blank" rel="noopener noreferrer">
                            <img className="footer-icon" src={iconFacebook} alt="cone do Facebook" />
                        </a>
                        <a href="/" target="_blank" rel="noopener noreferrer">
                            <img className="footer-icon" src={iconEmail} alt="cone do Gmail" />
                        </a>
                    </div>
                </div>
                <div className="footer-content">
                    <div className="footer-title">
                        <p>
                            Projeto
                        </p>
                    </div>
                    <div className="footer-options">
                        <a className="options-foot-1" href="/">
                            Sobre
                        </a>
                        <a className="options-foot-1" href="/">
                            Organizao
                        </a>
                        <a className="options-foot-1" href="/">
                            Notcias
                        </a>
                    </div>
                </div>
                <div className="footer-content">
                    <div className="footer-title">
                        <p>
                            Cuide da Sade
                        </p>
                    </div>
                    <div className="footer-options">
                        <a className="options-foot-1" href="/">
                            Check up online
                        </a>
                        <a className="options-foot-1" href="/">
                            Avaliar qualidade de vida
                        </a>
                        <a className="options-foot-1" href="/">
                            Dicas saudveis
                        </a>
                    </div>
                </div>
                <div className="footer-content">
                    <div className="footer-title">
                        <p>
                            Poltica
                        </p>
                    </div>
                    <div className="footer-options">
                        <a className="options-foot-1" href="/">
                            Termos
                        </a>
                        <a className="options-foot-1" href="/">
                            Privacidade
                        </a>
                        <a className="options-foot-1" href="/">
                            Diretrizes
                        </a>
                    </div>
                </div>
                <div className="footer-content">
                    <div className="footer-title">
                        <p>
                            Apoio
                        </p>
                    </div>
                    <div className="footer-collaborators">
                        <img className="footer-collaborators-img" src={imgCollaboratorsDOMLogo} alt="Logo Misso com Sade"/>
                    </div>
                </div>
            </div>
            <Divider width={'95%'}  height={'1px'} color="var(--c-text-secundary)"/>
            <div className="footer-end-panel">
                <img className="footer-logo" src={imgFooterLogo} alt="Logo Misso com Sade"/>
                <p className="footer-title-init">Misso</p>
                <p className="footer-title-mid">com</p>
                <p className="footer-title-end">Sade</p>
            </div>
            
            <div className="copyright-text">
                <p>&copy; Misso com Sade - Uma ao em prol do Evangelho</p>
            </div>
        </div>
    );
};

export default Footer;