import React from "react";

import './InfoIMC.css';

import imageInfoIMC from '../../../../../assets/testspage/imc/imc-info.png';

const InfoIMC = () => {
    return(
        <div className="infoIMC-container">
            <h1 className="infoIMC-title">IMC</h1>
            <h4 className="infoIMC-subtitle">Informativo</h4>
            <img className="ImageInfoIMC" src={imageInfoIMC} alt="Imagem informando a tabela de peso do IMC"/>
            <div className="infoIMC-painel">
                <div className="infoIMC-painel-text">
                    <p className="infoIMC-text-title">Por que o IMC  essncial para a Qualidade de Vida?</p>
                    <p className="infoIMC-text">
                        O grande benefcio do IMC  que ele oferece uma forma prtica e acessvel de monitorar a sade. 
                        Com um simples clculo,  possvel ter uma noo geral do estado do corpo e, a partir disso, 
                        adotar medidas preventivas, como ajustar a alimentao ou comear uma rotina de exerccios. 
                        Essa capacidade de fazer ajustes precoces  um dos pontos mais importantes para a manuteno da 
                        sade, pois ajuda a evitar o desenvolvimento de complicaes antes que elas se tornem problemas 
                        srios. Alm disso, o peso ideal, indicado pelo IMC, vai alm da sade fsica. Ele tambm est 
                        associado ao bem-estar psicolgico. Pessoas que mantm um peso saudvel tendem a se sentir mais 
                        confiantes, energizadas e satisfeitas consigo mesmas. Isso impacta diretamente a autoestima, 
                        diminuindo o risco de problemas emocionais como ansiedade e depresso, que muitas vezes esto 
                        associados a distrbios de peso.
                    </p>
                    <p className="infoIMC-text">
                        Outro aspecto importante  que o controle do IMC contribui para uma vida mais ativa. Quando o peso est 
                        equilibrado, o corpo funciona de forma mais eficiente, proporcionando maior disposio para realizar atividades 
                        fsicas, melhorar o condicionamento e at envelhecer de maneira mais saudvel. Isso se reflete em um estilo de 
                        vida mais dinmico, menos fadiga e mais energia para as atividades dirias. Por fim, manter o IMC dentro do intervalo 
                        saudvel  uma questo de longevidade e de viver bem. O controle do peso corporal  fundamental para garantir que o 
                        corpo continue funcionando de maneira eficiente ao longo dos anos, preservando a sade fsica e mental. Assim, o 
                        IMC no s ajuda a prevenir doenas, mas tambm promove uma qualidade de vida superior, permitindo que as pessoas 
                        vivam mais e com mais sade, bem-estar e vitalidade.
                    </p>
                </div>
            </div>

        </div>
    );  
};

export default InfoIMC;