import React from "react";

import imgLogoAbout from '../../../../assets/logo/logo-about.png';
import './ProjectAbout.css';

const projectAbout = () => {
    return(
        <div className="projectAbout-container">
            <h1 className="about-title">Sobre</h1>
            <h4 className="about-subtitle">O projeto</h4>
            <div className="about-painel">
                <div className="about-painel-text">
                    <p className="about-text-title">Bem-vindo ao "Misso com Sade"!</p>
                    <p className="about-text">
                        Sabemos que a vida ministerial exige muita dedicao, tempo e energia para cuidar de sua congregao e de seu ministrio. 
                        No entanto,  fundamental que voc tambm cuide da sua sade fsica, mental e espiritual. Nosso site foi criado especialmente para ajudar pastores, 
                        missionrios (as) e lderes evanglicos a encontrar dicas prticas e eficazes para manter o equilbrio entre a sade e o ministrio.
                    </p>
                    <p className="about-text">
                        Aqui, voc encontrar orientaes sobre avaliaes mdicas necessrias, alimentao saudvel, prticas de exerccio, sade emocional e dicas para fortalecer 
                        sua vida espiritual, tudo com o objetivo de garantir que voc esteja sempre bem, para melhor servir queles que Deus confiou aos seus cuidados.
                    </p>
                    <p className="about-text-bold">
                        Seu bem-estar  essencial para que sua misso seja plena e abenoada. Vamos juntos nessa caminhada rumo a uma vida mais saudvel e equilibrada!
                    </p>
                </div>
                <img className="logoAbout" src={imgLogoAbout} alt="Logo Misso com Sade"/>
            </div>
            <div className="about-spacer">
                <p className="spacer-text">
                    E disse-lhes: Ide por todo o mundo e pregai o evangelho a toda criatura.
                </p>
                <p className="spacer-subtext">
                    Marcos 16:15
                </p>
            </div>
        </div>
    );  
};

export default projectAbout;