import React from "react";

import './IntroIMC.css';

import imgLogoAbout from '../../../../../assets/testspage/imc/imc-intro.png';

const IntroIMC = () => {
    return(
        <div className="introIMC-container">
            <h1 className="introIMC-title">IMC</h1>
            <h4 className="introIMC-subtitle">Introduo</h4>
            <div className="introIMC-painel">
                <div className="introIMC-painel-text">
                    <p className="introIMC-text-title">Por que o IMC  essncial para a Qualidade de Vida?</p>
                    <p className="introIMC-text">
                    O ndice de Massa Corporal (IMC)  uma ferramenta simples, mas extremamente eficaz, quando o 
                    assunto  monitorar a sade e garantir uma melhor qualidade de vida. Ele serve como um 
                    indicador do equilbrio entre peso e altura, ajudando a identificar se uma pessoa est dentro 
                    de uma faixa de peso considerada saudvel. Essa avaliao  essencial porque o peso corporal 
                    tem um impacto direto na sade fsica e mental, influenciando desde a preveno de doenas at 
                    o bem-estar dirio.
                    </p>
                    <p className="introIMC-text">
                    Manter o IMC dentro da faixa ideal est diretamente relacionado  preveno de doenas crnicas 
                    graves, como problemas cardiovasculares, diabetes tipo 2 e condies respiratrias. Quando uma pessoa 
                    est com sobrepeso ou obesidade, a probabilidade de desenvolver essas doenas aumenta drasticamente, 
                    o que pode comprometer sua qualidade de vida a longo prazo. Alm disso, o excesso de peso pode sobrecarregar 
                    articulaes, levando a problemas como dores nas costas e nos joelhos, dificultando atividades cotidianas e 
                    limitando a mobilidade.
                    </p>
                </div>
                <img className="ImageIMC" src={imgLogoAbout} alt="Imagem representando uma pessoa se pesando"/>
            </div>
        </div>
    );  
};

export default IntroIMC;