import React from "react";

import './EquipAbout.css';
import Divider from "../../../../components/divider/Divider.jsx";
import imgMembersOne from "../../../../assets/members-picture/profile-test.png";
import imgMembersTwo from "../../../../assets/members-picture/profile-test1.png";

import iconLinkedin from "../../../../assets/icons/linkedin.svg";
import iconEmail from "../../../../assets/icons/email.svg";
import iconUser from "../../../../assets/icons/user.svg";

const equipAbout = () => {

    const scroll = (direction) => {
        const container = document.querySelector(".equipAbout-panel-card");
        const scrollAmount = container.offsetWidth / 1;
        container.scrollLeft += direction * scrollAmount;
    };
      

    return(
        <div className="equipAbout-container">
            <h1 className="equipAbout-title">Sobre</h1>
            <h4 className="equipAbout-subtitle">A equipe</h4>
            <div className="equipAbout-panel">
                <div className="equipAbout-panel-text">
                    <p className="equipAbout-text-title">Nossa Misso</p>
                    <p className="equipAbout-text">
                        Nosso compromisso  cuidar de quem cuida. Este projeto foi criado especialmente para apoiar pastores, missionrios e lderes em sua jornada, promovendo a sade integral  fsica, emocional e espiritual. Entendemos que a dedicao ao ministrio muitas vezes pode levar ao desgaste, e por isso queremos ser uma fonte de suporte e cuidado.
                        Aqui, voc encontrar dicas prticas para uma vida mais saudvel, apoio para enfrentar os desafios do dia a dia e orientao sobre os exames necessrios para monitorar sua sade. Mais do que isso, somos uma ponte entre voc e uma rede de profissionais de sade cristos comprometidos em ajudar nessa misso.
                    </p>
                    <p className="equipAbout-text">
                        
                    </p>
                    <p className="equipAbout-text">
                        Nosso trabalho  facilitar o acesso a consultas, exames e recursos indispensveis para cuidar de si mesmo, permitindo que recupere foras e retorne ao campo ministerial com vigor renovado. Acreditamos que sua sade  essencial para continuar servindo a Deus com excelncia e impactando vidas. Porque cuidar de voc  tambm cuidar da misso que lhe foi confiada.
                    </p>
                </div>
            </div>
            <Divider color="var(--c-text-primary)" height={"1px"} width={"10%"}/>
            <div className="equipAbout-panel">
                <p className="equipAbout-text-title">Membros</p>
                <div className="equipAbout-panel-card-container">
                    <button className="equipAbout-carousel-button prev" onClick={() => scroll(-1)}></button>
                    <div className="equipAbout-panel-card">
                        {/* Cards de Membros de Equipe */}
                        <div className="equipAbout-card">
                            <p className="equipAbout-card-title">Funo</p>
                            <div className="equipAbout-card-model">
                                <img className="equipAbout-card-img" src={imgMembersOne} alt="Imagem do membro participante do projeto"/>
                                <p className="equipAbout-card-name">Nome</p>
                                <div className="equipAbout-card-socials">
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconUser} alt="cone Perfil" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconEmail} alt="cone Email" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconLinkedin} alt="cone LinkedIn" />
                                    </a>
                                </div>
                            </div>
                        </div>

                        <div className="equipAbout-card">
                            <p className="equipAbout-card-title">Funo</p>
                            <div className="equipAbout-card-model">
                                <img className="equipAbout-card-img" src={imgMembersTwo} alt="Imagem do membro participante do projeto"/>
                                <p className="equipAbout-card-name">Nome</p>
                                <div className="equipAbout-card-socials">
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconUser} alt="cone Perfil" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconEmail} alt="cone Email" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconLinkedin} alt="cone LinkedIn" />
                                    </a>
                                </div>
                            </div>

                        </div>

                        <div className="equipAbout-card">
                            <p className="equipAbout-card-title">Funo</p>
                            <div className="equipAbout-card-model">
                                <img className="equipAbout-card-img" src={imgMembersOne} alt="Imagem do membro participante do projeto"/>
                                <p className="equipAbout-card-name">Nome</p>
                                <div className="equipAbout-card-socials">
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconUser} alt="cone Perfil" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconEmail} alt="cone Email" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconLinkedin} alt="cone LinkedIn" />
                                    </a>
                                </div>
                            </div>
                        </div>

                        <div className="equipAbout-card">
                            <p className="equipAbout-card-title">Funo</p>
                            <div className="equipAbout-card-model">
                                <img className="equipAbout-card-img" src={imgMembersOne} alt="Imagem do membro participante do projeto"/>
                                <p className="equipAbout-card-name">Nome</p>
                                <div className="equipAbout-card-socials">
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconUser} alt="cone Perfil" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconEmail} alt="cone Email" />
                                    </a>
                                    <a href="/" target="_blank" rel="noopener noreferrer">
                                        <img className="equipAbout-social-icon" src={iconLinkedin} alt="cone LinkedIn" />
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <button className="equipAbout-carousel-button next" onClick={() => scroll(1)}></button>
                </div>
            </div>
        </div>
    );  
};

export default equipAbout;